  methods: {
    init() {
      if ("yezhu" == this.userTableName) {
        this.dynamicProp.xingbie = ",".split(",");
      }
    },
    setSession() {
      localStorage.setItem("sessionForm", JSON.stringify(this.sessionForm));
    },
    onSubmit(formName) {
      if (`yezhu` == this.userTableName && this.sessionForm.touxiang != null) {
        this.sessionForm.touxiang = this.sessionForm.touxiang.replace(
          new RegExp(this.$config.baseUrl, "g"),
          ""
        );
      }
      if (
        `gongsi` == this.userTableName &&
        this.sessionForm.gongsifengmian != null
      ) {
        this.sessionForm.gongsifengmian =
          this.sessionForm.gongsifengmian.replace(
            new RegExp(this.$config.baseUrl, "g"),
            ""
          );
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http
            .post(this.userTableName + "/update", this.sessionForm)
            .then((res) => {
              if (res.data.code == 0) {
                this.setSession();
                this.$message({
                  message: "",
                  type: "success",
                  duration: 1500,
                });
              }
            });
        } else {
          return false;
        }
      });
    },
    yezhutouxiangHandleAvatarSuccess(fileUrls) {
      this.sessionForm.touxiang = fileUrls;
    },
    gongsigongsifengmianHandleAvatarSuccess(fileUrls) {
      this.sessionForm.gongsifengmian = fileUrls;
    },
    handleClick(tab, event) {
      switch (event.target.outerText) {
        case "":
          tab.$router.push("/index/center");
          break;
        case "":
          this.passwordForm = {
            password: "",
            newpassword: "",
            repassword: "",
          };
          this.$forceUpdate();
          break;
        case "":
          tab.$router.push("/index/myForumList");
          break;
        case "":
          localStorage.setItem("storeupType", 1);
          tab.$router.push("/index/storeup");
          break;
        default:
          tab.$router.push(`/index/${tab.name}?centerType=1`);
      }

      this.title = event.target.outerText;
    },
    async updatePassword() {
      this.$refs["passwordForm"].validate(async (valid) => {
        if (valid) {
          var password = "";
          if (this.sessionForm.mima) {
            password = this.sessionForm.mima;
          } else if (this.sessionForm.password) {
            password = this.sessionForm.password;
          }
          if (this.passwordForm.password != password) {
            this.$message.error("");
            return;
          }
          if (this.passwordForm.newpassword != this.passwordForm.repassword) {
            this.$message.error("");
            return;
          }
          if (this.userTableName == "yezhu") {
          }
          this.sessionForm.password = this.passwordForm.newpassword;
          this.sessionForm.mima = this.passwordForm.newpassword;
          this.$http
            .post(`${this.userTableName}/update`, this.sessionForm)
            .then(({ data }) => {
              if (data && data.code === 0) {
                this.$message({
                  message: ",",
                  type: "success",
                  duration: 1500,
                  onClose: () => {},
                });
                this.setSession();
              } else {
                this.$message.error(data.msg);
              }
            });
        }
      });
    },
    logout() {
      localStorage.clear();
      Vue.http.headers.common["Token"] = "";
      this.$router.push("/index/home");
      this.activeIndex = "0";
      localStorage.setItem("keyPath", this.activeIndex);
      this.$forceUpdate();
      this.$message({
        message: "",
        type: "success",
        duration: 1500,
      });
    },
    hasBack(name) {
      switch (name) {
        case "":
          return false;
          break;
        default:
          return true;
      }
    },
  },
};
</script>

