<template>
<div :style='{"width":"100%","padding":"30px 7% 40px","margin":"0px auto","position":"relative","background":"#fff"}'>
    <el-form
	  :style='{"border":"1px solid #eee","width":"100%","padding":"30px","position":"relative","background":"#fcfcfc"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="180px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="gongsimingcheng">
            <el-input v-model="ruleForm.gongsimingcheng" 
                placeholder="" clearable :disabled=" false  ||ro.gongsimingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="fuzeren">
            <el-input v-model="ruleForm.fuzeren" 
                placeholder="" clearable :disabled=" false  ||ro.fuzeren"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" v-if="type!='cross' || (type=='cross' && !ro.gongsifengmian)" prop="gongsifengmian">
            <file-upload
            tip=""
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.gongsifengmian?ruleForm.gongsifengmian:''"
            @change="gongsifengmianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' class="upload" v-else label="" prop="gongsifengmian">
                <img v-if="ruleForm.gongsifengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.gongsifengmian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.gongsifengmian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="lianxifangshi">
            <el-input v-model="ruleForm.lianxifangshi" 
                placeholder="" clearable :disabled=" false  ||ro.lianxifangshi"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="kongjian">
            <el-input v-model="ruleForm.kongjian" 
                placeholder="" clearable :disabled=" false  ||ro.kongjian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="ruanzhuang">
            <el-input v-model="ruleForm.ruanzhuang" 
                placeholder="" clearable :disabled=" false  ||ro.ruanzhuang"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="yingzhuang">
            <el-input v-model="ruleForm.yingzhuang" 
                placeholder="" clearable :disabled=" false  ||ro.yingzhuang"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="baojia">
			<el-input-number v-model="ruleForm.baojia" placeholder="" :readonly="ro.baojia"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="zhuangxiufengge">
            <el-input v-model="ruleForm.zhuangxiufengge" 
                placeholder="" clearable :disabled=" false  ||ro.zhuangxiufengge"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="tiwenshijian">
              <el-date-picker
				  :disabled=" false  ||ro.tiwenshijian"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  v-model="ruleForm.tiwenshijian" 
                  type="datetime"
                  placeholder="">
              </el-date-picker>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="yezhuzhanghao">
            <el-input v-model="ruleForm.yezhuzhanghao" 
                placeholder="" clearable :disabled=" false  ||ro.yezhuzhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="yezhuxingming">
            <el-input v-model="ruleForm.yezhuxingming" 
                placeholder="" clearable :disabled=" false  ||ro.yezhuxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="fangwumingcheng">
            <el-input v-model="ruleForm.fangwumingcheng" 
                placeholder="" clearable :disabled=" false  ||ro.fangwumingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="louceng">
            <el-input v-model="ruleForm.louceng" 
                placeholder="" clearable :disabled=" false  ||ro.louceng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="fangjianhao">
            <el-input v-model="ruleForm.fangjianhao" 
                placeholder="" clearable :disabled=" false  ||ro.fangjianhao"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="mianji">
            <el-input v-model="ruleForm.mianji" 
                placeholder="" clearable :disabled=" false  ||ro.mianji"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="fangxing">
            <el-input v-model="ruleForm.fangxing" 
                placeholder="" clearable :disabled=" false  ||ro.fangxing"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="weizhi">
            <el-input v-model="ruleForm.weizhi" 
                placeholder="" clearable :disabled=" false  ||ro.weizhi"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="" prop="zhuangxiuwenti">
            <editor 
                :style='{"minHeight":"250px","padding":"0","boxShadow":"0 0 0px rgba(75,223,201,.5)","margin":"0","borderColor":"#eee","backgroundColor":"#fff","borderRadius":"0","borderWidth":"1px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.zhuangxiuwenti" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"4px","background":"#F5BB00","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit"></el-button>
        <el-button :style='{"border":"0px solid rgba(64, 158, 255, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"4px","background":"#333","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()"></el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				gongsimingcheng : false,
				fuzeren : false,
				gongsifengmian : false,
				lianxifangshi : false,
				kongjian : false,
				ruanzhuang : false,
				yingzhuang : false,
				baojia : false,
				zhuangxiufengge : false,
				tiwenshijian : false,
				zhuangxiuwenti : false,
				yezhuzhanghao : false,
				yezhuxingming : false,
				fangwumingcheng : false,
				louceng : false,
				fangjianhao : false,
				mianji : false,
				fangxing : false,
				weizhi : false,
				shhf : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          gongsimingcheng: '',
          fuzeren: '',
          gongsifengmian: '',
          lianxifangshi: '',
          kongjian: '',
          ruanzhuang: '',
          yingzhuang: '',
          baojia: '',
          zhuangxiufengge: '',
          tiwenshijian: '',
          zhuangxiuwenti: '',
          yezhuzhanghao: '',
          yezhuxingming: '',
          fangwumingcheng: '',
          louceng: '',
          fangjianhao: '',
          mianji: '',
          fangxing: '',
          weizhi: '',
        },


        rules: {
          gongsimingcheng: [
          ],
          fuzeren: [
          ],
          gongsifengmian: [
          ],
          lianxifangshi: [
          ],
          kongjian: [
          ],
          ruanzhuang: [
          ],
          yingzhuang: [
          ],
          baojia: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          zhuangxiufengge: [
          ],
          tiwenshijian: [
          ],
          zhuangxiuwenti: [
          ],
          yezhuzhanghao: [
          ],
          yezhuxingming: [
          ],
          fangwumingcheng: [
          ],
          louceng: [
          ],
          fangjianhao: [
          ],
          mianji: [
          ],
          fangxing: [
          ],
          weizhi: [
          ],
          shhf: [
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.tiwenshijian = this.getCurDateTime()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 
      download(file){
        window.open(`${file}`)
      },
      // 
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='gongsimingcheng'){
              this.ruleForm.gongsimingcheng = obj[o];
              this.ro.gongsimingcheng = true;
              continue;
            }
            if(o=='fuzeren'){
              this.ruleForm.fuzeren = obj[o];
              this.ro.fuzeren = true;
              continue;
            }
            if(o=='gongsifengmian'){
              this.ruleForm.gongsifengmian = obj[o].split(",")[0];
              this.ro.gongsifengmian = true;
              continue;
            }
            if(o=='lianxifangshi'){
              this.ruleForm.lianxifangshi = obj[o];
              this.ro.lianxifangshi = true;
              continue;
            }
            if(o=='kongjian'){
              this.ruleForm.kongjian = obj[o];
              this.ro.kongjian = true;
              continue;
            }
            if(o=='ruanzhuang'){
              this.ruleForm.ruanzhuang = obj[o];
              this.ro.ruanzhuang = true;
              continue;
            }
            if(o=='yingzhuang'){
              this.ruleForm.yingzhuang = obj[o];
              this.ro.yingzhuang = true;
              continue;
            }
            if(o=='baojia'){
              this.ruleForm.baojia = obj[o];
              this.ro.baojia = true;
              continue;
            }
            if(o=='zhuangxiufengge'){
              this.ruleForm.zhuangxiufengge = obj[o];
              this.ro.zhuangxiufengge = true;
              continue;
            }
            if(o=='tiwenshijian'){
              this.ruleForm.tiwenshijian = obj[o];
              this.ro.tiwenshijian = true;
              continue;
            }
            if(o=='zhuangxiuwenti'){
              this.ruleForm.zhuangxiuwenti = obj[o];
              this.ro.zhuangxiuwenti = true;
              continue;
            }
            if(o=='yezhuzhanghao'){
              this.ruleForm.yezhuzhanghao = obj[o];
              this.ro.yezhuzhanghao = true;
              continue;
            }
            if(o=='yezhuxingming'){
              this.ruleForm.yezhuxingming = obj[o];
              this.ro.yezhuxingming = true;
              continue;
            }
            if(o=='fangwumingcheng'){
              this.ruleForm.fangwumingcheng = obj[o];
              this.ro.fangwumingcheng = true;
              continue;
            }
            if(o=='louceng'){
              this.ruleForm.louceng = obj[o];
              this.ro.louceng = true;
              continue;
            }
            if(o=='fangjianhao'){
              this.ruleForm.fangjianhao = obj[o];
              this.ro.fangjianhao = true;
              continue;
            }
            if(o=='mianji'){
              this.ruleForm.mianji = obj[o];
              this.ro.mianji = true;
              continue;
            }
            if(o=='fangxing'){
              this.ruleForm.fangxing = obj[o];
              this.ro.fangxing = true;
              continue;
            }
            if(o=='weizhi'){
              this.ruleForm.weizhi = obj[o];
              this.ro.weizhi = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.yezhuzhanghao!=''&&json.yezhuzhanghao) || json.yezhuzhanghao==0){
                this.ruleForm.yezhuzhanghao = json.yezhuzhanghao
            }
            if((json.yezhuxingming!=''&&json.yezhuxingming) || json.yezhuxingming==0){
                this.ruleForm.yezhuxingming = json.yezhuxingming
            }
          }
        });

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 
      // 
      info() {
        this.$http.get(`zhuangxiuwenti/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 
      onSubmit() {

			//
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('zhuangxiuwenti/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 


								this.$http.post(`zhuangxiuwenti/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`zhuangxiuwenti/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
		// 
		back() {
			this.$router.go(-1);
		},
      gongsifengmianUploadChange(fileUrls) {
          this.ruleForm.gongsifengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
