<script>

export default {
    //
    data() {
		return {
            pageFlag : '',
			tableName: '',
			registerForm: {},
			forgetForm: {},
			rules: {},
			requiredRules: {},
            yezhuxingbieOptions: [],
		}
    },
	mounted() {
		if(this.$route.query.pageFlag=='register'){
			
			this.tableName = this.$route.query.role;
			if(this.tableName=='yezhu'){
				this.registerForm = {
					yezhuzhanghao: '',
					mima: '',
					mima2: '',
					yezhuxingming: '',
					xingbie: '',
					lianxifangshi: '',
					touxiang: '',
					pquestion: '',
					panswer: '',
				}
			}
			if(this.tableName=='gongsi'){
				this.registerForm = {
					gongsimingcheng: '',
					mima: '',
					mima2: '',
					fuzeren: '',
					gongsifengmian: '',
					lianxifangshi: '',
					gongsiyouxiang: '',
					gongsidizhi: '',
					chuanglishijian: this.getCurDate(),
					shejituandui: '',
					gongsijianjie: '',
					sfsh: '',
					shhf: '',
					clicktime: '',
					clicknum: '',
					storeupnum: '',
				}
			}
			if ('yezhu' == this.tableName) {
				this.requiredRules.yezhuzhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yezhu' == this.tableName) {
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('yezhu' == this.tableName) {
				this.requiredRules.yezhuxingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('gongsi' == this.tableName) {
				this.requiredRules.gongsimingcheng = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('gongsi' == this.tableName) {
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
		}
	},
    created() {
		this.pageFlag = this.$route.query.pageFlag;
		if(this.$route.query.pageFlag=='register'){
		  if ('yezhu' == this.tableName) {
			this.rules.yezhuzhanghao = [{ required: true, message: '', trigger: 'blur' }];
		  }
		  if ('yezhu' == this.tableName) {
			this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
		  }
		  if ('yezhu' == this.tableName) {
			this.rules.yezhuxingming = [{ required: true, message: '', trigger: 'blur' }];
		  }
			this.yezhuxingbieOptions = ",".split(',');
		  if ('yezhu' == this.tableName) {
			this.rules.lianxifangshi = [{ required: true, validator: this.$validate.isMobile, trigger: 'blur' }];
		  }
		  if ('gongsi' == this.tableName) {
			this.rules.gongsimingcheng = [{ required: true, message: '', trigger: 'blur' }];
		  }
		  if ('gongsi' == this.tableName) {
			this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
		  }
		  if ('gongsi' == this.tableName) {
			this.rules.gongsiyouxiang = [{ required: true, validator: this.$validate.isEmail, trigger: 'blur' }];
		  }
			this.registerForm.chuanglishijian = this.getCurDate();
		  if ('gongsi' == this.tableName) {
			this.rules.clicknum = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
		  }
		  if ('gongsi' == this.tableName) {
			this.rules.storeupnum = [{ required: true, validator: this.$validate.isIntNumer, trigger: 'blur' }];
		  }
		}
    },
    //
    methods: {
		changeRules(name){
			if(this.requiredRules[name]){
				return true
			}
			return false
		},
      // uuid
      getUUID () {
        return new Date().getTime();
      },
        // 
      yezhutouxiangUploadChange(fileUrls) {
          this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
      gongsigongsifengmianUploadChange(fileUrls) {
          this.registerForm.gongsifengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },

        // 


//
        getSecurity(){
            this.tableName = 'yezhu'
            if(!this.tableName) {
                this.$message.error("");
                return false
            }
            if(!this.forgetForm.username){
                this.$message.error("");
                return false
            }
            this.$http({
                url: this.tableName+`/security?username=` + this.forgetForm.username,
                method: "get",
            }).then(({
                data
            }) => {
                if (data.data && data.code === 0) {
                    this.registerForm = data.data;
                    this.pageFlag="security2"
                } else {
                    if(!data.data) {
                        this.$message.error("");
                    } else {
                        this.$message.error(data.msg);
                    }
                }
            });
        },
        //
        validateSecurity(){
            let that = this
            if(this.registerForm.panswer!=this.registerForm.myanswer){
                this.$message.error("");
                return false
            }
            this.$message.success("");
            setTimeout(()=>{
                this.pageFlag="security3";
            },1000)
        },
        // 
        updatePassword() {
            if (!this.registerForm.newpassword) {
                this.$message.error("");
                return;
            }
            if (this.registerForm.newpassword != this.registerForm.repassword) {
                this.$message.error("");
                return;
            }
            this.registerForm.mima = this.registerForm.newpassword;
            this.registerForm.password = this.registerForm.newpassword;
            this.$http.post(this.tableName+`/update`, this.registerForm).then(res => {
              if (res.data.code === 0) {
                this.$message({
                    message: "",
                    type: "success",
                    duration: 1500,
                    onClose: () => {
                      this.$router.push('/login');
                    }
                });
              } else {
                this.$message.error(res.data.msg);
              }
            });
        },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var url=this.tableName+"/register";
				if((!this.registerForm.yezhuzhanghao) && `yezhu` == this.tableName){
					this.$message.error(``);
					return
				}
               if(`yezhu` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
				if((!this.registerForm.mima) && `yezhu` == this.tableName){
					this.$message.error(``);
					return
				}
				if((!this.registerForm.yezhuxingming) && `yezhu` == this.tableName){
					this.$message.error(``);
					return
				}
					if(`yezhu` == this.tableName && this.registerForm.lianxifangshi &&(!this.$validate.isMobile2(this.registerForm.lianxifangshi))){
						this.$message.error(``);
						return
					}
			  if(this.tableName=='gongsi'){
				  this.registerForm.sfsh = ''
			  }
				if((!this.registerForm.gongsimingcheng) && `gongsi` == this.tableName){
					this.$message.error(``);
					return
				}
               if(`gongsi` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
                this.$message.error(``);
                return
               }
				if((!this.registerForm.mima) && `gongsi` == this.tableName){
					this.$message.error(``);
					return
				}
					if(`gongsi` == this.tableName && this.registerForm.gongsiyouxiang &&(!this.$validate.isEmail2(this.registerForm.gongsiyouxiang))){
						this.$message.error(``);
						return
					}
            this.$http.post(url, this.registerForm).then(res => {
              if (res.data.code === 0) {
                this.$message({
                  message: '',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.$router.push('/login');
                  }
                });
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

