<template>
  <div
    :style="{
      width: '100%',
      padding: '30px 7% 40px',
      margin: '0px auto',
      position: 'relative',
      background: '#fff',
    }"
  >
    <el-form
      :style="{
        border: '1px solid #eee',
        width: '100%',
        padding: '30px',
        position: 'relative',
        background: '#fcfcfc',
      }"
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="180px"
    >
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label=""
        prop="title"
      >
        <el-input
          v-model="ruleForm.title"
          placeholder=""
          clearable
          :disabled="false || ro.title"
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label=""
        prop="subtitle"
      >
        <el-input
          v-model="ruleForm.subtitle"
          placeholder=""
          clearable
          :disabled="false || ro.subtitle"
        ></el-input>
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label="1"
        v-if="type != 'cross' || (type == 'cross' && !ro.picture1)"
        prop="picture1"
      >
        <file-upload
          tip="1"
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.picture1 ? ruleForm.picture1 : ''"
          @change="picture1UploadChange"
        ></file-upload>
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        class="upload"
        v-else
        label="1"
        prop="picture1"
      >
        <img
          v-if="ruleForm.picture1.substring(0, 4) == 'http'"
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          :src="ruleForm.picture1.split(',')[0]"
          width="100"
          height="100"
        />
        <img
          v-else
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          v-for="(item, index) in ruleForm.picture1.split(',')"
          :src="baseUrl + item"
          width="100"
          height="100"
        />
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label="2"
        v-if="type != 'cross' || (type == 'cross' && !ro.picture2)"
        prop="picture2"
      >
        <file-upload
          tip="2"
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.picture2 ? ruleForm.picture2 : ''"
          @change="picture2UploadChange"
        ></file-upload>
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        class="upload"
        v-else
        label="2"
        prop="picture2"
      >
        <img
          v-if="ruleForm.picture2.substring(0, 4) == 'http'"
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          :src="ruleForm.picture2.split(',')[0]"
          width="100"
          height="100"
        />
        <img
          v-else
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          v-for="(item, index) in ruleForm.picture2.split(',')"
          :src="baseUrl + item"
          width="100"
          height="100"
        />
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label="3"
        v-if="type != 'cross' || (type == 'cross' && !ro.picture3)"
        prop="picture3"
      >
        <file-upload
          tip="3"
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.picture3 ? ruleForm.picture3 : ''"
          @change="picture3UploadChange"
        ></file-upload>
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        class="upload"
        v-else
        label="3"
        prop="picture3"
      >
        <img
          v-if="ruleForm.picture3.substring(0, 4) == 'http'"
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          :src="ruleForm.picture3.split(',')[0]"
          width="100"
          height="100"
        />
        <img
          v-else
          class="upload-img"
          style="margin-right: 20px"
          v-bind:key="index"
          v-for="(item, index) in ruleForm.picture3.split(',')"
          :src="baseUrl + item"
          width="100"
          height="100"
        />
      </el-form-item>
      <el-form-item
        :style="{ padding: '10px', margin: '0 0 10px', background: 'none' }"
        label=""
        prop="content"
      >
        <editor
          :style="{
            minHeight: '250px',
            padding: '0',
            boxShadow: '0 0 0px rgba(75,223,201,.5)',
            margin: '0',
            borderColor: '#eee',
            backgroundColor: '#fff',
            borderRadius: '0',
            borderWidth: '1px',
            width: '100%',
            borderStyle: 'solid',
            height: 'auto',
          }"
          v-model="ruleForm.content"
          class="editor"
          action="file/upload"
        >
        </editor>
      </el-form-item>

      <el-form-item :style="{ padding: '0', margin: '0' }">
        <el-button
          :style="{
            border: '0',
            cursor: 'pointer',
            padding: '0',
            margin: '0 20px 0 0',
            outline: 'none',
            color: 'rgba(255, 255, 255, 1)',
            borderRadius: '4px',
            background: '#F5BB00',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
          type="primary"
          @click="onSubmit"
          ></el-button
        >
        <el-button
          :style="{
            border: '0px solid rgba(64, 158, 255, 1)',
            cursor: 'pointer',
            padding: '0',
            margin: '0',
            outline: 'none',
            color: '#fff',
            borderRadius: '4px',
            background: '#333',
            width: '128px',
            lineHeight: '40px',
            fontSize: '14px',
            height: '40px',
          }"
          @click="back()"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    let self = this;
    return {
      id: "",
      baseUrl: "",
      ro: {
        title: false,
        subtitle: false,
        content: false,
        picture1: false,
        picture2: false,
        picture3: false,
      },
      type: "",
      userTableName: localStorage.getItem("UserTableName"),
      ruleForm: {
        title: "",
        subtitle: "",
        content: "",
        picture1: "",
        picture2: "",
        picture3: "",
      },

      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        subtitle: [],
        content: [{ required: true, message: "", trigger: "blur" }],
        picture1: [],
        picture2: [],
        picture3: [],
      },
      centerType: false,
    };
  },
  computed: {},
  components: {},
  created() {
    if (this.$route.query.centerType) {
      this.centerType = true;
    }
    //this.bg();
    let type = this.$route.query.type ? this.$route.query.type : "";
    this.init(type);
    this.baseUrl = this.$config.baseUrl;
  },
  methods: {
    getMakeZero(s) {
      return s < 10 ? "0" + s : s;
    },
    // 
    download(file) {
      window.open(`${file}`);
    },
    // 
    init(type) {
      this.type = type;
      if (type == "cross") {
        var obj = JSON.parse(localStorage.getItem("crossObj"));
        for (var o in obj) {
          if (o == "title") {
            this.ruleForm.title = obj[o];
            this.ro.title = true;
            continue;
          }
          if (o == "subtitle") {
            this.ruleForm.subtitle = obj[o];
            this.ro.subtitle = true;
            continue;
          }
          if (o == "content") {
            this.ruleForm.content = obj[o];
            this.ro.content = true;
            continue;
          }
          if (o == "picture1") {
            this.ruleForm.picture1 = obj[o].split(",")[0];
            this.ro.picture1 = true;
            continue;
          }
          if (o == "picture2") {
            this.ruleForm.picture2 = obj[o].split(",")[0];
            this.ro.picture2 = true;
            continue;
          }
          if (o == "picture3") {
            this.ruleForm.picture3 = obj[o].split(",")[0];
            this.ro.picture3 = true;
            continue;
          }
        }
      } else if (type == "edit") {
        this.info();
      }

      if (
        localStorage.getItem("raffleType") &&
        localStorage.getItem("raffleType") != null
      ) {
        localStorage.removeItem("raffleType");
        setTimeout(() => {
          this.onSubmit();
        }, 300);
      }
    },

    // 
    // 
    info() {
      this.$http
        .get(`aboutus/detail/${this.$route.query.id}`, { emulateJSON: true })
        .then((res) => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
    },
    // 
    onSubmit() {
      //
      var crossuserid;
      var crossrefid;
      var crossoptnum;
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          if (this.type == "cross") {
            var statusColumnName = localStorage.getItem("statusColumnName");
            var statusColumnValue = localStorage.getItem("statusColumnValue");
            if (statusColumnName && statusColumnName != "") {
              var obj = JSON.parse(localStorage.getItem("crossObj"));
              if (!statusColumnName.startsWith("[")) {
                for (var o in obj) {
                  if (o == statusColumnName) {
                    obj[o] = statusColumnValue;
                  }
                }
                var table = localStorage.getItem("crossTable");
                this.$http.post(table + "/update", obj).then((res) => {});
              } else {
                crossuserid = Number(localStorage.getItem("frontUserid"));
                crossrefid = obj["id"];
                crossoptnum = localStorage.getItem("statusColumnName");
                crossoptnum = crossoptnum.replace(/\[/, "").replace(/\]/, "");
              }
            }
          }
          if (crossrefid && crossuserid) {
            this.ruleForm.crossuserid = crossuserid;
            this.ruleForm.crossrefid = crossrefid;
            var params = {
              page: 1,
              limit: 10,
              crossuserid: crossuserid,
              crossrefid: crossrefid,
            };
            this.$http
              .get("aboutus/list", {
                params: params,
              })
              .then((res) => {
                if (res.data.data.total >= crossoptnum) {
                  this.$message({
                    message: localStorage.getItem("tips"),
                    type: "error",
                    duration: 1500,
                  });
                  return false;
                } else {
                  // 

                  this.$http
                    .post(
                      `aboutus/${
                        this.ruleForm.id
                          ? "update"
                          : this.centerType
                          ? "save"
                          : "add"
                      }`,
                      this.ruleForm
                    )
                    .then((res) => {
                      if (res.data.code == 0) {
                        this.$message({
                          message: "",
                          type: "success",
                          duration: 1500,
                          onClose: () => {
                            this.$router.go(-1);
                          },
                        });
                      } else {
                        this.$message({
                          message: res.data.msg,
                          type: "error",
                          duration: 1500,
                        });
                      }
                    });
                }
              });
          } else {
            this.$http
              .post(
                `aboutus/${
                  this.ruleForm.id ? "update" : this.centerType ? "save" : "add"
                }`,
                this.ruleForm
              )
              .then((res) => {
                if (res.data.code == 0) {
                  this.$message({
                    message: "",
                    type: "success",
                    duration: 1500,
                    onClose: () => {
                      this.$router.go(-1);
                    },
                  });
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: "error",
                    duration: 1500,
                  });
                }
              });
          }
        }
      });
    },
    // uuid
    getUUID() {
      return new Date().getTime();
    },
    // 
    back() {
      this.$router.go(-1);
    },
    picture1UploadChange(fileUrls) {
      this.ruleForm.picture1 = fileUrls.replace(
        new RegExp(this.$config.baseUrl, "g"),
        ""
      );
    },
    picture2UploadChange(fileUrls) {
      this.ruleForm.picture2 = fileUrls.replace(
        new RegExp(this.$config.baseUrl, "g"),
        ""
      );
    },
    picture3UploadChange(fileUrls) {
      this.ruleForm.picture3 = fileUrls.replace(
        new RegExp(this.$config.baseUrl, "g"),
        ""
      );
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
